body {
  background: -moz-radial-gradient(
    circle,
    rgba(238, 174, 202, 1) 0%,
    rgba(148, 187, 233, 1) 100%
  );
  background: -webkit-radial-gradient(
    circle,
    rgba(238, 174, 202, 1) 0%,
    rgba(148, 187, 233, 1) 100%
  );
}

.titleStyle {
  color: brown;
  font-size: 50px;
  font-style: italic;
}

.priceStyle {
  color: #4698fc;
  font-size: 30px;
  font-style: italic;
  margin: 0;
}

.titleDisable {
  color: violet;
  composes: titleStyle;
}

.listStyle {
  list-style: none;
  display: flex;
  justify-content: space-around;
}

