.mainTitle {
  /* text-align: center;
   text-shadow: -4px -4px 0px #D2EAD7;
   background: repeating-linear-gradient(45deg, #6EB495, #6EB495 1px, white 2px, white 3px);
   -webkit-background-clip: text;
   -webkit-text-fill-color: transparent;
   font-size: 40px; */
  text-align: center;
  font-size: 70px;
  font-style: italic;
  /* text-shadow: 2px 2px 0 #b362c4, 4px 4px 0 #bb9ece; */
  animation: flow 10s ease-in-out infinite;
  background: linear-gradient(-60deg, #904e95, #904e95, #e73c7e, #ee7752);
  background-size: 300%;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

@keyframes flow {
  0% {
    background-position: 0 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0 50%;
  }
}

.imgStyle {
  -webkit-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
}

.hover {
  height: 360px;
  width: 256px;
  position: relative;
}

.wrapper::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: -moz-radial-gradient(
    circle,
    rgba(238, 174, 202, 1) 0%,
    rgba(148, 187, 233, 1) 100%
  ); /* FF3.6-15 */
  background-image: -webkit-radial-gradient(
    circle,
    rgba(238, 174, 202, 1) 0%,
    rgba(148, 187, 233, 1) 100%
  );
  opacity: 0;
  transition: all 0.3s ease;
}

.wrapper:hover:before {
  opacity: 0.8;
  background-image: -moz-radial-gradient(
    circle,
    rgba(238, 174, 202, 1) 0%,
    rgba(148, 187, 233, 1) 100%
  ); /* FF3.6-15 */
  background-image: -webkit-radial-gradient(
    circle,
    rgba(238, 174, 202, 1) 0%,
    rgba(148, 187, 233, 1) 100%
  );
  /* background-image: -webkit-linear-gradient(90deg, #fce38a 0%, #f38181 100%); */
}
